@font-face {
  font-family: "Meragen-BW2e5";
  src: local("Meragen-BW2e5"), url(./assets/Meragen-BW2e5.woff) format("woff");
  font-family: "Goodies demo";
  src: local("Goodies demo"), url(./assets/Goodies\ demo.otf) format("otf");
}
body {
  margin: 0;
  font-family: "Goodies demo", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Navbar Styles */
.navbar-main-container {
  display: flex;
  flex-direction: row;
  position: fixed;
  margin-top: -8%;
  z-index: 3;
}
.navbar-second-container {
  background: linear-gradient(
    224.68deg,
    rgba(238, 57, 254, 0.95) 24.86%,
    rgba(231, 41, 121, 0.658777) 78.08%,
    rgba(238, 28, 79, 0.573471) 91.39%,
    rgba(247, 82, 112, 0.526042) 98.79%,
    rgba(255, 33, 153, 0) 100%
  );
  align-self: center;
  padding-top: 2%;
  padding-bottom: 1%;

  width: 100%;
  z-index: 3;
}
.logo {
  width: 4%;
  align-self: center;
  margin-inline: 1%;
  margin-left: 35%;
}
.header-text {
  font-size: xx-large;
  align-self: center;
  font-family: "Goodies demo";
  /* padding-left: 40%; */
  color: white;
}
.header-icon-container {
  display: flex;
  flex-direction: row;
}

/* ProductContainer Styles */
.product-container-main-container {
  border-width: 0.5;
  border-style: solid;
  padding-inline: 1%;
  padding-top: 1%;
  padding-bottom: 1%;
  border-color: #373232;
  display: flex;
  flex-direction: column;
  margin-inline: 4%;
}
.user-name-container {
  width: 50%;
  border-width: 1;
  display: flex;
  flex-direction: row;
}
.user-icon {
  width: 70%;
}
.user-name-text {
  align-self: center;
}
.product-image {
  width: 100%;
}
.buy-now-button {
  width: 100%;
  background-color: #676565;
}
.button-container {
  width: 17%;
  align-self: center;
  margin-left: 30%;
}
/* Home Styles */
.home-main-container {
  margin-top: 8%;
}
.main-text {
  color: #fff;
  font-weight: bold;
  font-size: 300%;
  flex-wrap: wrap;
  display: flex;
  padding-top: 10%;
  padding-left: 5%;
  font-family: Georgia, "Times New Roman", Times, serif;
  align-self: center;
}
.input-link-container {
  width: 40%;
  margin-left: 30%;
  padding-left: 3%;
  padding-top: 0.5%;
  padding-bottom: 0.5%;
  border-style: solid;
  border-image: linear-gradient(50deg, #f93d97, #8527f9 59.95%, #fb0c7099) 3;
  border-width: 4px;
  font-size: large;
  font-family: Goodies demo;
  font-weight: bold;
  justify-content: space-evenly;
  background-color: white;
}
.icon-container {
  justify-content: center;
}
.mobile-login {
  display: none;
}
@media (max-width: 600px) {
  .home-main-container {
    margin-top: 20%;
  }
  .product-container-main-container {
    margin-top: 5%;
  }
  .navbar-main-container {
    margin-top: -20%;
  }
  .navbar-second-container {
    display: flex;
    flex-direction: row;
  }
  .logo {
    display: none;
  }
  .button-container {
    margin-left: 0%;
    background: linear-gradient(50deg, #f93d97, #5b0830);
    height: 10rem;
    width: 7rem;
    justify-content: center;
  }
  .header-text {
    margin-left: 20%;
    align-self: center;
  }
  .main-text {
    font-size: 250%;
    padding-right: 15%;
  }
  .input-link-container {
    width: 80%;
    margin-left: 10%;
    padding-left: 3%;
    font-size: medium;
    justify-content: none;
  }
  .mobile-login {
    display: block;
    padding-left: 15%;
    align-self: center;
  }
}
